import List from './until'
import Home from './view/home'
import React from 'react'
// import load from './until'
import './App.css'
import {Switch, Redirect} from 'react-router'
import {BrowserRouter as Router, Route} from 'react-router-dom'
// const Home = load('./view/home.js')
// const List = load('./view/list.js')
function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route
            path='/home'
            component={Home}
          />
          <Route path='/list' component={List} />
          <Redirect to='/home' />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
